import React, { useEffect }  from "react";
import { useSelector, useDispatch } from "react-redux";
import { getRecipes, changePage } from "../actions";
import "./Home.css";

import Cards from './Cards/Recipes.jsx'
import Paginacion from './Paginacion/Paginacion.jsx'

export default function Home () {
    
    const dispatch = useDispatch();
    
    const recipePerPage = 9;
    var allrecipes = useSelector((state) => state.allrecipes);
    useEffect(() => {
        dispatch(getRecipes());
    }, [dispatch]);
    let recipes = useSelector((state) => state.recipes);
    


    const totalPages = Math.ceil(allrecipes.length / recipePerPage);
    const actualPage = useSelector((state) => state.page);

    if (recipes.length > 1) {
        recipes = recipes.slice(
            (actualPage - 1) * recipePerPage,
            actualPage * recipePerPage
        );
    }


    return (
        <div className="main">
            <div className="sortfiltBtns">
                Sorting
            </div>
            <div className="recipes">
                <div className="paginacion">
                    {totalPages !== 0 && (
                        <Paginacion
                            total={totalPages}
                            onChange={(page) => {
                                dispatch(changePage(page));
                            }}
                        />
                    )}
                </div>
                <div className="recipesDiv">
                    {!allrecipes.length && <h1>Cargando...</h1>}
                    <Cards recipes={recipes}/>
                </div>
            </div>
        </div>
    )
};