import React from "react";
import { Link } from "react-router-dom"
import "./Recipe.css";

export default function Card ({id, name, img, dietType, healthScore}) {
    return (
      <Link to={`/home/recipe/${id}`} className="Card" id={id}>
        <div className="imgDiv">
          <img className="imgReceta" src={img} width="80" height="80" alt="" />
        </div>
        <div className="infoCard">
          <div className="nameDiv">
            <h3 className="name">{name}</h3>
          </div>

          <h4 className="healthScore">Health Score: {healthScore}</h4>

          <div className="diets">
            <p className="diettitle">Diets: {dietType}</p>
          </div>
        </div>
      </Link>
    );
};
