import React from "react";
import "./Recipes.css";
import error from '../../assets/1.png'

import Card from "./Recipe";

export default function Cards( { recipes } ) {

  if (recipes.length) {
    return (
      <div className="cards">
        { recipes.map((r) => (
          <Card
            id={r.id}
            name={r.name}
            img={r.image ? r.image : error}
            dietType={r.dietType ? r.dietType : []}
            healthScore={r.healthScore}
            key={r.id}
          />
        ))}
      </div>
    );
  } else {
    return <div>Sin rercetas...</div>;
  }
}
