import React from "react";
import { useSelector } from "react-redux";
import "./Paginacion.css";

export default function Paginacion({ total, onChange }) {

    const actualPage = useSelector((state) => state.page);
    const numpages = () => {
        const pages = [];
        for (let i = 0; i < total; i++) {
            let pag = i + 1;
            pages.push(
                <p
                onClick={() => onChange(pag)}
                className={actualPage === pag ? "active" : "inactive"}
                key={`pagenumber${pag}`}
                >
                {pag}
                </p>
            );
        }
        return pages;
    };

    return (
        <div className="pagination">
            <div className="paginas">{numpages()}</div>
        </div>
    );
};
